.skills {
  padding: 50px 50px;
}

.skills__title {
  margin: 0 0 50px;
}

.skills__text {
  margin: 0 0 30px;
}

.skills__list {
  .reset-list;

  display: flex;
  flex-flow: row wrap;

  counter-reset: counter-name;
}

.skills__item {
  width: 50%;

  text-align: center;

  position: relative;

  padding-top: 50px;
  margin-bottom: 25px;

  &::before {
    counter-increment: counter-name;
    content: counter(counter-name);

    width: 40px;
    height: 40px;


    position: absolute;
    top: 1px;
    left: 50%;

    border: 4px solid @border-color-counter;
    border-radius: 50%;

    color: @color-counter;
    background-color: @background-color-counter;

    padding-top: 6px;
    box-sizing: border-box;
  }
}
