.header {
  background-color: @background-header;
}

.header__list {
  .reset-list;

  display: flex;
  justify-content: space-between;

  padding: 50px;
}

.header__item:hover .header__link {
  color: @background-page ;
}

.header__item:active .header__link {
  color: @background-footer;
}

.header__link {
  text-decoration: none;
  color: @header-link
}