@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  width: 1200px;
  height: auto;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  background-color: #B9C4C9;
  padding: 0;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}
.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.preloader {
  position: fixed;
  inset: 0;
  background: #fff1e1;
  z-index: 1001;
}
.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}
.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}
.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}
@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%,
  100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.about-me {
  padding: 50px;
}
.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #C9D1C8;
  margin: 0;
}
.contacts__link {
  text-decoration: none;
  color: #B6452C;
}
.courses {
  padding: 50px 50px;
}
.courses__title {
  margin: 0 0 50px;
}
.courses__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.courses__item {
  width: 30%;
  text-align: center;
}
.courses__img {
  width: 300px;
  height: 400px;
  padding: 15px 0;
}
.education {
  color: white;
  background-color: #063852;
  padding: 50px 50px;
}
.experience {
  padding: 50px 50px;
}
.experience__title {
  margin: 0 0 50px;
}
.experience__container-rosneft {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}
.experience__text {
  width: 300px;
  font-size: 18px;
  line-height: 22px;
  margin: 0 50px 0 0;
}
.experience__container-sng {
  display: flex;
  justify-content: space-around;
  margin-bottom: 50px;
}
.experience__container-sng > .experience__text {
  width: 400px;
  order: 2;
}
.experience__picture-i-and-sng,
.experience__picture-rvs {
  width: 300px;
  height: 300px;
}
.experience__picture-i-and-sng {
  order: 1;
}
.experience__picture-rvs {
  order: 3;
}
.experience > .experience__text {
  width: 100%;
  font-size: 18px;
  line-height: 22px;
}
.footer {
  display: flex;
  justify-content: space-around;
  background-color: #B7B3B6;
}
.header {
  background-color: #04202C;
}
.header__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  padding: 50px;
}
.header__item:hover .header__link {
  color: #B9C4C9;
}
.header__item:active .header__link {
  color: #B7B3B6;
}
.header__link {
  text-decoration: none;
  color: #FFFFFF;
}
.hobby {
  color: white;
  background-color: #063852;
  padding: 50px 50px;
}
.hobby__title {
  margin: 0 0 50px;
}
.hobby__text {
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 10px;
}
.hobby__button {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  line-height: 20px;
  padding: 0;
  margin: 0;
}
.hobby__answer--hide {
  display: none;
}
.photo-gallery {
  padding: 50px 50px;
  background-color: #063852;
}
.photo-gallery__title {
  color: white;
  margin: 0 0 50px;
}
.photo-gallery__container {
  width: 800px;
  height: 800px;
  display: flex;
  flex-flow: row wrap;
  margin: 0 auto;
}
.photo-gallery__slider {
  height: 400px;
  list-style: none;
  position: relative;
  padding: 0;
  margin: 0 0 20px 0;
  overflow: hidden;
}
.photo-gallery__slide {
  width: 400px;
  height: 400px;
  position: absolute;
  transition: all 0.5s;
}
.photo-gallery__picture,
.photo-gallery__image {
  width: 100%;
  height: 100%;
}
.photo-gallery__button-container {
  display: flex;
  justify-content: space-between;
}
.photo-gallery__button {
  width: 150px;
  height: 50px;
  font-size: 20px;
  line-height: 22px;
  border: 3px solid;
  border-radius: 20px;
  color: black;
  background-color: #ffffff;
}
.portfolio {
  color: white;
  background-color: #063852;
  padding: 50px 50px;
}
.portfolio__title {
  margin: 0 0 30px;
}
.portfolio__text {
  margin: 0 0 20px;
}
.portfolio__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
}
.portfolio__link {
  text-decoration: none;
  color: #B6452C;
}
.portfolio__link:hover {
  color: #EB8A3E;
}
.portfolio__item-description {
  margin-bottom: 10px;
}
.portfolio__item-description:last-child {
  margin-bottom: 0;
}
.preview {
  display: flex;
  justify-content: center;
  padding: 50px 0;
}
.preview__title {
  font-size: 26px;
  line-height: 30px;
  text-align: center;
  margin: 0;
}
.skills {
  padding: 50px 50px;
}
.skills__title {
  margin: 0 0 50px;
}
.skills__text {
  margin: 0 0 30px;
}
.skills__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  counter-reset: counter-name;
}
.skills__item {
  width: 50%;
  text-align: center;
  position: relative;
  padding-top: 50px;
  margin-bottom: 25px;
}
.skills__item::before {
  counter-increment: counter-name;
  content: counter(counter-name);
  width: 40px;
  height: 40px;
  position: absolute;
  top: 1px;
  left: 50%;
  border: 4px solid #A1D6E2;
  border-radius: 50%;
  color: #2C7873;
  background-color: #F1F1F2;
  padding-top: 6px;
  box-sizing: border-box;
}
@keyframes shake {
  0%,
  50%,
  100% {
    transform: rotate(0deg);
  }
  10%,
  30% {
    transform: rotate(-10deg);
  }
  20%,
  40% {
    transform: rotate(10deg);
  }
}
.social__container {
  display: flex;
  justify-content: center;
}
.social__link {
  width: 40px;
  height: 40px;
}
.social__vk,
.social__instagram {
  width: 40px;
  height: 40px;
}
.social__vk path,
.social__instagram path {
  width: 40px;
  height: 40px;
}
/*.social__vk path:hover,
.social__instagram path:hover {
    animation: shake 2s ease-in-out
}*/
.social__vk:hover,
.social__instagram:hover {
  animation: shake 1.2s ease-in-out;
}
