.hobby {
  color: @color-text-even;
  background-color: @background-education;
  
  padding: 50px 50px;
}

.hobby__title {
  margin: 0 0 50px
}

.hobby__text {
  font-size: 16px;
  line-height: 20px;

  margin: 0 0 10px;
}

.hobby__button {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  line-height: 20px;

  padding: 0;
  margin: 0;
}

.hobby__answer--hide {
  display: none;
}