.experience {
  padding: 50px 50px;
}

.experience__title {
  margin: 0 0 50px;
}

.experience__container-rosneft {
  display: flex;
  justify-content: center;

  margin-bottom: 50px;
}

.experience__text {
  width: 300px;

  font-size: 18px;
  line-height: 22px;

  margin: 0 50px 0 0;
}

.experience__container-sng {
  display: flex;
  justify-content: space-around;

  margin-bottom: 50px;
}

.experience__container-sng > .experience__text {
  width: 400px;

  order: 2;
}

.experience__picture-i-and-sng,
.experience__picture-rvs {
  width: 300px;
  height: 300px;
}

.experience__picture-i-and-sng {
  order: 1;
}

.experience__picture-rvs {
  order: 3;
}

.experience > .experience__text {
  width: 100%;

  font-size: 18px;
  line-height: 22px;
}