.photo-gallery {
  padding: 50px 50px;

  background-color: @background-education;
}

.photo-gallery__title {
  color: white;
  
  margin: 0 0 50px;
}


.photo-gallery__container {
  width: 800px;
  height: 800px;

  display: flex;
  flex-flow: row wrap;

  //background-color: @background-counter;

  margin: 0 auto;
}

.photo-gallery__slider {
  height: 400px;

  list-style: none;

  position: relative;

  padding: 0;
  margin: 0 0 20px 0;

  overflow: hidden;
}

.photo-gallery__slide {
  width: 400px;
  height: 400px;

  position: absolute;

  transition: all 0.5s;
}

.photo-gallery__picture,
.photo-gallery__image {
  width: 100%;
  height: 100%;
}

.photo-gallery__button-container {
  display: flex;
  justify-content: space-between;
}

.photo-gallery__button {
  width: 150px;
  height: 50px;

  font-size: 20px;
  line-height: 22px;

  border: 3px solid;
  border-radius: 20px;

  color: @color-photo-gallery-button;
  background-color: @background-photo-gallery-button;
}